<template>
    <div>
        <div class="kd-page-title">
            信息确认
        </div>
        <div class="kd-page-content-wrapper">
            <div class="wrap-info">
                <div class="oneLine">
                    <div class="item">
                        <p class="label">
                            姓名：
                        </p>
                        <p class="message">
                            {{ liveRegisterInfo.registerName }}
                        </p>
                    </div>
                    <div class="item">
                        <p class="labelAfter">
                            性别：
                        </p>
                        <p class="message">
                            {{ liveRegisterInfo.sex === 1 ? '男' : '女' }}
                        </p>
                    </div>
                    <div class="item">
                        <p class="labelAfter">
                            身份证：
                        </p>
                        <p class="message">
                            {{ liveRegisterInfo.idNum }}
                        </p>
                    </div>
                </div>
                <div class="item">
                    <p class="label">
                        现居地址：
                    </p>
                    <p class="message">
                        {{ liveRegisterInfo.currentResidentAddress }}
                    </p>
                </div>
                <div class="item">
                    <p class="label">
                        所属街道：
                    </p>
                    <p class="message">
                        {{ liveRegisterInfo.street }}
                    </p>
                </div>
                <div class="item">
                    <p class="label">
                        所属社区：
                    </p>
                    <p class="message">
                        {{ liveRegisterInfo.community }}
                    </p>
                </div>

                <div class="footer">
                    温馨提示：请认真核实并确认以下登记信息是否发生变更，如已变更，
                    请前往当前社区派出窗口进行 信息修改，如未变更，请点击“下一步”按钮进行确认操作。
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="openTip"
            />
        </div>
        <alert-msg
            v-if="alertOn"
            :alertSure="sure"
            :alertCancel="cancel"
            tip-text="请认真核对以上信息，确认无误！"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import alertMsg from '@/components/alert-msg';
import audioObj from '@/assets/media/household/5.mp3';
import { playAudio } from '../../../../utils/media';

export default {
    name: 'LivePersonInfo',
    components: { alertMsg },
    data () {
        return {
            alertOn: false
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        openTip () {
            this.alertOn = true;
        },
        goUp () {
            this.changeStep('up');
        },
        sure () {
            this.alertOn = false;
            this.$nextTick(() => {
                this.changeStep('down');
            });
        },
        cancel () {
            this.alertOn = false;
        }
    },
    computed: {
        ...mapState('live', {
            liveRegisterInfo: 'liveRegisterInfo'
        })
    },
    mounted () {
        this.audioObj = playAudio(audioObj);
        this.audioObj.play();
    },
    beforeDestroy () {
        this.audioObj.destroy();
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        padding: 20px;
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .oneLine {
            display: flex;
        }

        .item {
            font-size: 2.8vh;
            padding-bottom: 3vh;
            display: flex;
        }

        .label {
            color: #e0ebf796;
            width: 17vw;
            text-align: right;
        }

        .labelAfter {
            color: #e0ebf796;
            margin-left: 3vh;
        }

        .message {
            color: #ffffff;
        }

        .footer {
            box-sizing: border-box;
            padding: 3vh 5vh;
            font-size: 2.2vh;
            color: #e0ebf796;
            line-height: 4vh;

        }

    }
</style>
