<template>
    <div>
        <div class="kd-page-title">
            验证手机
        </div>
        <div class="kd-page-content-wrapper kd-page-padding wrap-info">
            <verify-code
                @getVerifyCode="getVerifyCode"
                ref="verifyCode"
            />
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
            />
        </div>
    </div>
</template>

<script>
import { getPhoneCode, avlidPhoneCode } from '@/service/human/people';
import verifyCode from '@/components/verify-code';
import { mapMutations } from 'vuex';

export default {
    name: 'LiveCheckPhone',
    components: { verifyCode },
    data () {
        return {
            fullScreenLoading: false
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('live', ['setLiveHandleBusinessInfo']),
        async getVerifyCode () {
            const res = await getPhoneCode({
                phone: this.$refs.verifyCode.item.phoneNumber
            });
            if (res && res.code === '0') {
                console.log('获取验证码成功');
            } else {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        },
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            if (this.$refs.verifyCode.submission()) {
                const res = await avlidPhoneCode({
                    phone: this.$refs.verifyCode.item.phoneNumber,
                    code: this.$refs.verifyCode.item.verifyCode
                });
                if (res && res.code === '0') { // 0:无法办证;1：居住证新办2：居住证补办，3：居住证挂失补办;4:已在审核状态，请等待
                    if (res.result && res.result.status === 0) { // 无法办证
                        this.changeTipPage({ message: '无法办证' });
                    } else if (res.result && res.result.status === 4) { // 已在审核状态，请等待
                        this.changeTipPage({ message: '已在审核状态，请等待' });
                    } else {
                        this.setLiveHandleBusinessInfo({ businessType: res.result });
                        this.changeStep('down');
                    }
                } else {
                    this.changeTipPage({
                        message: res.message,
                        enableBack: true
                    });
                }
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        justify-content: center;
        align-items: center;
    }
</style>
