<template>
    <div>
        <div class="kd-page-title">
            请正对摄像头，脸部置于框内保持不动
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <div v-if="!taked">
                <img
                    v-if="nowAvatar"
                    height="400"
                    :src="nowAvatar"
                >
                <div
                    v-else
                    class="loadingImg el-icon-loading"
                />
            </div>
            <div v-if="taked">
                <img
                    v-if="finallyPhoto"
                    height="400"
                    :src="finallyPhoto"
                >
                <div
                    v-else
                    class="loadingImg el-icon-loading"
                />
            </div>
            <div class="timeText">
                {{ timeClockNum && timeClockNum > 1 ? timeClockNum-1 : '' }}
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                v-if="!taked"
                icon="kedaIcon-zhaoxiangji"
                text="拍照"
                :button-handle="takePhoto"
                :but-disable="timeClockNum"
            />
            <kd-button
                v-if="!taked"
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />

            <kd-button
                v-if="taked"
                icon="kedaIcon-zhaoxiangji"
                text="重拍"
                :button-handle="takeAgain"
                :but-disable="isUpdate"
            />
            <kd-button
                v-if="taked"
                icon="kedaIcon-duihao2"
                text="提交"
                :button-handle="upPhoto"
                :but-disable="!finallyPhoto"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { sendWork } from '@/service/human/people';
import { wsSend } from '@/utils/websocket';
import { cancelPrefix } from '@/utils/tools';
import audioReady from '@/assets/media/common/8.mp3';
import audioTake from '@/assets/media/household/9.mp3';
import { playAudio } from '../../../../utils/media';

export default {
    name: 'LiveFacePic',
    computed: {
        ...mapState('permanent', {
            nowAvatar: 'singleReflexImgSrc',
            singleReflexWebSocket: 'singleReflexWebSocket',
            singleReflexTakeImgSrc: 'singleReflexTakeImgSrc'
        }),
        ...mapState('live', {
            liveHandleBusinessInfo: 'liveHandleBusinessInfo'
        })
    },
    data () {
        return {
            taked: false,
            isUpdate: false,
            timeClockNum: '',
            finallyPhoto: '',
            audioReady: null,
            audioTake: null,
            timeId: null
        };
    },
    watch: {
        singleReflexTakeImgSrc (newValue) {
            this.audioReady.pause();
            this.audioTake.play();
            this.timeClockNum = '';
            this.taked = true;
            this.finallyPhoto = newValue;
            wsSend(this.singleReflexWebSocket, {
                module: 'Camera',
                directive: 'stop'
            });
        }
    },
    mounted () {
        this.audioReady = playAudio(audioReady);
        this.audioTake = playAudio(audioTake);
        this.audioReady.play();
        wsSend(this.singleReflexWebSocket, {
            module: 'Camera',
            directive: 'reOpen'
        });
        wsSend(this.singleReflexWebSocket, {
            module: 'Camera',
            directive: 'start'
        });
        wsSend(this.lightWebSocket, 'lightCtrl@B5:ON');
    },
    beforeDestroy () {
        this.audioReady.destroy();
        this.audioTake.destroy();
        clearInterval(this.timeId);
        this.timeId = null;
        wsSend(this.lightWebSocket, 'lightCtrl@B5:OFF');
        wsSend(this.singleReflexWebSocket, {
            module: 'Camera',
            directive: 'stop'
        });
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('live', ['setLiveHandleBusinessInfo']),
        takePhoto () {
            this.timeClockNum = 4;
            this.timeId = setInterval(() => {
                this.timeClockNum = this.timeClockNum - 1;
                if (this.timeClockNum === 1) {
                    clearInterval(this.timeId);
                    this.timeId = null;
                    wsSend(this.singleReflexWebSocket, {
                        module: 'Camera',
                        directive: 'takePhoto'
                    });
                }
            }, 1000);
        },
        takeAgain () {
            this.taked = false;
            wsSend(this.singleReflexWebSocket, {
                module: 'Camera',
                directive: 'start'
            });
        },
        goUp () {
            this.changeStep('up');
        },
        async upPhoto () {
            this.isUpdate = true;
            this.setLiveHandleBusinessInfo({ identifiedPhoto: this.finallyPhoto });
            const res = await sendWork({
                businessType: this.liveHandleBusinessInfo.businessType,
                identifiedPhoto: cancelPrefix(this.finallyPhoto)
            });
            this.isUpdate = false;
            if (res && res.code === '0') {
                this.setLiveHandleBusinessInfo(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        justify-content: center;
        align-items: center;

        .loadingImg {
            font-size: 48px;
        }

        .timeText {
            margin-left: 20px;
            width: 80px;
            height: 400px;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 48px;
        }

        img {
            height: 400px;
        }
    }

</style>
