<template>
    <div>
        <div class="kd-page-title">
            业务确认
        </div>
        <div class="kd-page-content-wrapper kd-page-padding wrap-info">
            <div class="title">
                {{ des }}
            </div>
            <div
                class="but"
                onclick="goDown()"
            >
                {{ but }}
            </div>
            <div
                class="tio"
                v-if="liveHandleBusinessInfo.businessType === 2"
            >
                温馨提示：点击确定后，系统将为您进行居住证挂失操作，然后申请补办。
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goDown"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import audioObj from '@/assets/media/household/7.mp3';
import { playAudio } from '../../../../utils/media';

export default {
    name: 'LiveLiveMenu',
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        goUp () {
            this.changeStep('up');
        },
        goDown () {
            this.changeStep('down');
        }
    },
    data () {
        return {
            audioObj: null
        };
    },
    computed: {
        ...mapState('live', {
            liveHandleBusinessInfo: 'liveHandleBusinessInfo'
        }),
        des () {
            if (this.liveHandleBusinessInfo.businessType === 1) {
                return '系统查询结果显示，当前可进行：';
            } else if (this.liveHandleBusinessInfo.businessType === 3) {
                return '系统查询结果显示，您已对居住证进行挂失，当前可进行：';
            } else {
                return '系统查询结果显示，您未对居住证进行挂失，当前可进行：';
            }
        },
        but () {
            if (this.liveHandleBusinessInfo.businessType === 1) {
                return '居住证新办';
            } else if (this.liveHandleBusinessInfo.businessType === 2) {
                return '居住证挂失并补办';
            } else {
                return '居住证补办';
            }
        }
    },
    mounted () {
        this.audioObj = playAudio(audioObj);
        this.audioObj.play();
    },
    beforeDestroy () {
        this.audioObj.destroy();
    }
};
</script>

<style lang="scss" scoped>
    .title {
        color: rgba(255, 255, 255, .8);
        font-size: 22.4px;
        margin-top: 5vh;
        margin-left: 4vw;
    }

    .but {
        width: 30vw;
        height: 8vw;
        margin: 8vh auto 10vh;
        background: rgba(255, 255, 255, 0.1);
        border-radius: 6px;
        border: 2PX dashed rgba(242, 249, 255, 1);/*no*/
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 32px;
        color: rgba(255, 255, 255, .8);
    }

    .tio {
        margin-left: 4vw;
        color: rgba(255, 255, 255, .8);
        font-size: 19.2px;
    }
</style>
