<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />
        <person-info v-if="currentStep === steps.findIndex(step=>step.key === 'personInfo')"/>
        <check-phone v-if="currentStep === steps.findIndex(step=>step.key === 'checkPhone')"/>
        <live-menu v-if="currentStep === steps.findIndex(step=>step.key === 'liveMenu')"/>
        <face-pic v-if="currentStep === steps.findIndex(step=>step.key === 'facePic')"/>
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="liveHandleBusinessInfo"
            :foot-texts="footTexts"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { cloneDeep } from 'lodash';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import personInfo from './components/person-info';
import checkPhone from './components/check-phone';
import liveMenu from './components/live-menu';
import facePic from './components/face-pic';
import complete from '@/components/complete';
import process from '@/components/process';
import { checkStatusByUerRecognitionIdAndIdNum, liveFaceRecognition } from '@/service/human/people';

export default {
    name: 'LiveIndex',
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        personInfo,
        checkPhone,
        liveMenu,
        facePic,
        complete,
        process
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            binocularCamera: 'binocularCamera'
        }),
        ...mapState('live', {
            liveHandleBusinessInfo: 'liveHandleBusinessInfo'
        })
    },
    data () {
        return {
            types: ['', '居住证新办', '居住证补办', '居住证挂失补办'],
            footTexts: '温馨提示：业务已受理，请耐心等待，审核结果将会以短信告知。'
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'setSteps']),
        ...mapMutations('live', ['setLiveResidenceCardId', 'setLiveRegisterInfo']),
        async handleBusiness () {
            const res = await checkStatusByUerRecognitionIdAndIdNum({
                idNum: this.identifyCard.certNumber
            });
            if (res && res.code === '0') {
                // 用户登记状态 0:无效;1:本地市民;2:流动人口未登记;3:未在本区登记;4:已在本区登记;
                if (res.result.status === 0) {
                    this.changeTipPage({ message: '无效' });
                } else if (res.result.status === 1) {
                    this.changeTipPage({
                        type: 1,
                        message: '本地户籍，无需办理居住证业务',
                        imgName: 'imgHomeLocal'
                    });
                } else if (res.result.status === 2) {
                    this.changeTipPage({
                        message: '对不起，本市居住证系统内未查询到您的流动人口登记信息，请您先在居住地所属社区进行流动人口登记，登记完成方可办理此业务！',
                        imgName: 'imgText'
                    });
                } else if (res.result.status === 3) {
                    this.changeTipPage({
                        message: '请先前往社区派出所进行流动人口登记，方可办理居住证业务！',
                        imgName: 'imgText'
                    });
                } else if (res.result.status === 4) {
                    this.handleContrast();
                } else {
                    this.changeTipPage({ message: '无效' });
                }
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async handleContrast () {
            const res = await liveFaceRecognition({
                identifiedPhoto: this.binocularCamera
            });
            if (res && res.code === '0') {
                const data = cloneDeep(res.result);
                if (!Object.prototype.hasOwnProperty.call(data, 'sex')) {
                    // 不返回性别的时候根据身份证倒数第二位判断性别
                    const num = parseInt(data.idNum.substr(data.idNum.length - 2, 1), 10);
                    if (num % 2 === 0) {
                        data.sex = 2;
                    } else {
                        data.sex = 1;
                    }
                }
                this.setLiveRegisterInfo(data);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }
    },
    mounted () {
        this.setSteps([
            {
                name: '身份识别',
                isActive: true,
                visible: true,
                key: 'identifyCardPage'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'faceYitu'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'contrast'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'process'
            },
            {
                name: '信息确认',
                isActive: false,
                visible: true,
                key: 'personInfo'
            },
            {
                name: '手机校验',
                isActive: false,
                visible: true,
                key: 'checkPhone'
            },
            {
                name: '业务确认',
                isActive: false,
                visible: true,
                key: 'liveMenu'
            },
            {
                name: '照片采集',
                isActive: false,
                visible: true,
                key: 'facePic'
            },
            {
                name: '完成提交',
                isActive: false,
                visible: true,
                key: 'complete'
            }
        ]);
    }
};

</script>
